import React, { useState } from "react";

import StyledWatermarksContainer from "./StyledWatermarksContainer";
import { Checkbox, Collapse, Flex } from "antd";

import { IWatermark } from "../../types";
import { WATERMARKS_MAP_BY_STRING_ID } from "../../constants";

interface IProps {
  selectedIds: IWatermark["id"][];
  watermarks: IWatermark[];
  onSelect: (watermarks: IWatermark[]) => void;
}

const Watermarks = ({ selectedIds, watermarks, onSelect }: IProps) => {
  const [activeKey, setActiveKey] = useState(["watermarks"]);
  return (
    <StyledWatermarksContainer>
      <Collapse
        size="small"
        ghost
        expandIconPosition="start"
        activeKey={activeKey}
        onChange={(key) => setActiveKey(key)}
        items={[
          {
            key: "watermarks",
            label: "Watermarks",
            children: (
              <>
                {watermarks
                  .sort((a, b) => a.sortOrder - b.sortOrder)
                  .map((item) => (
                    <Flex
                      key={`${item.id}`}
                      gap={6}
                      align="center"
                      style={{ borderBottom: "1px solid #c5c5c5" }}
                    >
                      <div
                        style={{
                          padding: "2px 4px",
                          backgroundColor: item.color,
                        }}
                      >
                        <Checkbox
                          checked={selectedIds.includes(item.id)}
                          onChange={({ target: { checked } }) => {
                            const ids = checked
                              ? [...selectedIds, item.id]
                              : selectedIds.filter((id) => id !== item.id);
                            const watermarks = ids.reduce((result, id) => {
                              const watermark = WATERMARKS_MAP_BY_STRING_ID.get(
                                `${id}`
                              );
                              if (watermark) {
                                result.push(watermark);
                              }
                              return result;
                            }, [] as IWatermark[]);
                            onSelect(watermarks);
                          }}
                        ></Checkbox>
                      </div>
                      {item.watermarkName}
                    </Flex>
                  ))}
              </>
            ),
          },
        ]}
      />
    </StyledWatermarksContainer>
  );
};

export default Watermarks;
